.role {
  font-size: 3rem;
  letter-spacing: 0.2rem;
}
.role-a {
  letter-spacing: 0.9rem;
  margin-top: 45px;
}
.role-container {
  left: 8vw;
  top: 30%;
  position: absolute;
}
.profile {
  width: 95%;
  height: 95%;
  position: absolute;
  object-fit: contain;
  left: -15%;
  bottom: -5%;
}
.about-profile {
  width: 90%;
  height: 90%;
  position: absolute;
  object-fit: contain;
  z-index: 99;
}
.hie-container {
  position: absolute;
  z-index: 99;
  left: 65%;
  top: 55%;
}
.arrow-anim {
  height: 15vh;
  width: 18vw;
  margin-bottom: 23px;
}
.nav-btn {
  margin: 20px;
}

.social-container {
  position: absolute;
  display: flex;
  left: 14%;
  align-items: center;
  gap: 15px;
  z-index: 999;
  top: 77%;
}

.hire-me-container {
  display: flex;
  align-items: center;
  z-index: 999;
  position: absolute;
  left: 5%;
  top: 55%;
}

@media only screen and (max-width: 992px) {
  .role {
    font-size: 2.3rem;
  }
  .role-a {
    font-size: 1.2rem;
    letter-spacing: 0.7rem;
  }
  .social-container {
    top: 70%;
  }
}

@media only screen and (max-width: 768px) {
  .role {
    font-size: 1.3rem;
    letter-spacing: 0.1rem;
    /* text-align: center; */
  }
  .role-a {
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    margin-top: 25px;
  }
  .role-container {
    top: 25%;
    left: 5%;
    position: relative;
  }
  .hie-container {
    left: 55%;
    top: 80%;
  }
  .hire-me-container {
    display: none;
  }
  .about-profile {
    height: 80%;
    width: 150%;
    left: -30%;
  }
  .social-container {
    top: 85%;
  }
}
